.wrapper {
  width: 100%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: grid;
  place-items: center;
  grid-gap: 15px;
  gap: 15px;
}

.marginright {
  margin-right: "100px";
}

.nitrocontainer {
  height: 100px;
  width: 100px;
  border-radius: 8px;
  position: relative;
  cursor: pointer;
  color: #ffffff;
  background: radial-gradient(var(--theme-color-1), var(--theme-color-2));
  box-shadow: 0px 1px 10px #ffffff;
}

.nftcontainer {
  height: 230px;
  width: 100px;
  border-radius: 8px;
  position: relative;
  cursor: pointer;
  color: #ffffff;
  background: radial-gradient(var(--theme-color-1), var(--theme-color-2));
  box-shadow: 0px 1px 10px #ffffff;
}

input[type="checkbox"] {
  -webkit-appearance: none;
  position: relative;
  width: 100%;
  cursor: pointer;
}

input[type="checkbox"]:after {
  position: absolute;
  font-family: "Font Awesome 5 Free";
  font-weight: 400;
  content: "\F111";
  font-size: 18px;
  color: #ffffff;
  right: 10px;
  top: -5px;
}

input[type="checkbox"]:checked:after {
  font-weight: 900;
  content: "\F058";
  color: #ffffff;
}

.nftcontainer img {
  width: 70%;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 15%;
  cursor: pointer;

}

.nftcontainer-mobile img {
  width: 70%; 
  /* position: absolute; */
  margin: auto;
  left: 0;
  right: 0;
  top: 15%;
  cursor: pointer;

}

@media screen and (min-width: 950px) {
  .wrapper {
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
    gap: 10px;
  }

  .nftcontainer {
    height: 300px;
    width: 200px;
  }
  
  .nftcontainer-mobile {
    height: 300px;
    width: 200px;
  }

  input[type="checkbox"]:after {
    font-size: 22px;
  }
}

.nftdetail {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 5%;
  cursor: pointer;
  display: grid;
  place-items: center;
}

.errorMessage {
  height: 100px;
  width: 100px;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

select {
  -webkit-appearance: none;
  appearance: none;
  width: 250px;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 20px;
  letter-spacing: 0.5px;
  cursor: pointer;
  background-color: #00000010;
  color: #000000;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-size: 47px;
  background-position: 247px 0.5px;
  outline: none;
  box-shadow: 0 0 20px rgba(20, 20, 30, 0.25);
}

select::-ms-expand {
  display: none;
}

select option {
  background-color: #000000;
  letter-spacing: 1.2px;
  font-weight: 400;
  font-size: 18px;
}

.selected {
  display: none;
}





.vault_container:before {
  content: "";
  position: absolute;
  height: 280px;
  width: 280px;
  background: radial-gradient(var(--theme-color-1), var(--theme-color-2));
  border-radius: 50%;
  top: 100%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 50%;
}

.vault_card {
  height: 285px;
  width: 200px;
  position: relative;
  box-shadow: 0px 1px 10px #fafae6;
  border-radius: 5%;
  border: 10px solid var(--white-rgba);
}

.vault_front,
.vault_back {
  position: absolute;
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  border-radius: 15px;
  background-color: var(--white-rgba);
  border: 2px solid var(--white-rgba);
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transition: 1s;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

.vault_front {
  flex-direction: column;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
}

.vault_front img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 10px solid var(--white-rgba);
}

.vault_details {
  text-align: center;
}

h2 {
  font-size: 22px;
  font-weight: 500;
  letter-spacing: 0.5px;
  color: var(--white-shade-1);
}

.spanX {
  font-size: 14px;
  color: var(--white-shade-2);
  cursor: pointer;
}

a {
  cursor: pointer;
}

.vault_back {
  justify-content: space-around;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.vault_icon {
  display: grid;
  place-items: center;
  height: 65px;
  width: 65px;
  background-color: var(--white-rgba);
  font-size: 10px;
  border-radius: 8px;
  text-decoration: none;
  color: var(--white-shade-1);
}

.vault_icon:hover {
  background-color: var(--white-shade-1);
  color: var(--theme-color-1);
}

.vault_card:hover .vault_back {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  box-shadow: 0px 1px 10px #ffffff;
}

.vault_card:hover .vault_front {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
  box-shadow: 0px 1px 10px #ffffff;
}
 

